import { createStore } from 'vuex'

const userInfo = {
    state: () => ({
        access_token: null,
        refresh_token: null,
        id: null,
        username: null,
        status: null,
        roles: []
    }),
    mutations: {
        setToken(state, tokenResult) {
            state.access_token = tokenResult.access_token;
            state.refresh_token = tokenResult.refresh_token;
        },
        setUserInfo(state, userInfo) {
            state.id = userInfo.id;
            state.username = userInfo.username;
            state.status = userInfo.status;
            state.roles = userInfo.authorities.map((v) => v.authority);
        }
    },
    getters: {
        getAccessToken: state => {
            return state.access_token;
        }
    }
}

const storeModules = {
    modules: {
        userInfo
    }
}

const store = createStore(storeModules);

if (sessionStorage.getItem("ecm_store")) {
    store.replaceState(
        Object.assign(
            {},
            store.state,
            JSON.parse(sessionStorage.getItem("ecm_store"))
        )
    );
    sessionStorage.removeItem("ecm_store")
}

window.addEventListener("beforeunload", () => {
    sessionStorage.setItem("ecm_store", JSON.stringify(store.state));
});
export default store;