import store from '../stores'

const request = (method, url, body = {}, headers = {}, mode = 0) => {
    method = method.toUpperCase();
    return fetch(url, {
        method,
        headers: {
            'Content-Type': mode == 0 ? 'application/json' : 'application/x-www-form-urlencoded',
            'Accept': 'application/json',
            'Authorization': 'Bearer ' + store.state.userInfo.access_token,
            ...headers
        },
        body: resolveBody(body, method, mode),
    }).then(res => {
        return res.json();
    }).catch(error => {
        return {
            code: -1,
            message: error.message,
            data: url
        }
    })
}
const resolveBody = (data = {}, method, mode) => {
    if (method == 'GET') {
        return undefined;
    } else if (method == 'POST' && mode == 0) {
        return JSON.stringify(data);
    } else if (method == 'POST' && mode == 1) {
        let r = "";
        for (let key in data) {
            r += key + "=" + data[key] + "&";
        }
        return r;
    }
}

const pathVariable = /\{[^}]*\}/g
const parseUrl = (url = "", body = {}) => {
    const arr = url.match(pathVariable);
    if (arr) {
        arr.forEach(i => {
            url = url.replace(i, body[i.slice(1, i.length - 1)])
        })
    }
    return url;
}

export default request;
export const get = (path, body, headers) => request("GET", parseUrl(path, body), body, headers)
export const post = (path, body, headers, mode) => request("POST", parseUrl(path, body), body, headers, mode);
export const put = (path, body, headers) => request("PUT", parseUrl(path, body), body, headers);
export const del = (path, body, headers) => request("DELETE", parseUrl(path, body), body, headers);

export const buildTree = (arr, id, pid, chi) => {
    const tm = {};
    const ra = [];
    arr.forEach(item => {
        if (tm[item[id]]) {
            tm[item[id]] = { ...item, ...tm[item[id]] };
        } else {
            tm[item[id]] = { ...item };
        }
        if (item[pid]) {
            if (tm[item[pid]]) {
                if (tm[item[pid]][chi]) {
                    tm[item[pid]][chi].push(tm[item[id]]);
                } else {
                    tm[item[pid]][chi] = [tm[item[id]]];
                }
            } else {
                tm[item[pid]] = {};
                tm[item[pid]][chi] = [tm[item[id]]];
            }
        } else {
            ra.push(tm[item[id]]);
        }
    });
    return ra;
}

export const buildConnectTree = (arr = [], group = [], id, pid, children) => {
    arr.forEach(i => {
        const g = group.find(j => j[id] == i[pid])
        if (g) {
            let chi = g[children];
            if (!chi) {
                chi = [];
                g[children] = chi;
            }
            i.final = true;
            chi.push(i)
        }
    })
    const groupTree = buildTree(group, id, pid, children);
    return filterNullTree(groupTree, children, 'final');
}
const filterNullTree = (tree = [], chi, final) => {
    const r = [];
    for (let i in tree) {
        if (tree[i][chi]) {
            if (filterNullTree(tree[i][chi], chi, final).length > 0) {
                r.push(tree[i]);
            }
        } else {
            if (tree[i][final]) {
                r.push(tree[i]);
            }
        }
    }
    return r;
}