import { get, post } from '../utils'

export const doLogin = (data) => post('/ua/oauth/token', data, null, 1);
export const getUserInfo = () => get('/ua/api/current-user', null, null);
export const cacheConver = () => get('/file-server/pr/cache-cover', null, null)
export const getAllVideo = data => get('/file-server/api/video-query-all', data, null)