<template>
  <el-container>
    <el-header height="20px">
      <el-row>
        <el-col :span="4">ECM SYSTEM</el-col>
        <el-col :span="18"></el-col>
        <el-col :span="1"><i class="el-icon-user" @click="toLoginPage"></i></el-col>
      </el-row>
    </el-header>
    <el-main class="main-height">
      <router-view></router-view>
    </el-main>
    <el-footer height="20px" class="footer">
      <el-row>
        <el-col :span="4"></el-col>
        <el-col :span="17"></el-col>
        <el-col :span="2">Auth: CC</el-col>
      </el-row>
    </el-footer>
  </el-container>
</template>

<script>
import { getUserInfo } from "./ecm-api";

export default {
  name: "App",
  data() {
    return {};
  },
  mounted() {
    if (this.$store.state.userInfo.access_token) {
      getUserInfo().then((userInfoR) => {
        if (userInfoR.code == 0) {
          this.$store.commit("setUserInfo", userInfoR.data);
          this.$router.push("/home");
        } else {
          this.$router.push("/login");
          console.log("获取用户信息失败");
        }
      });
    } else {
      this.$router.push("/login");
    }
  },
  methods: {
    toLoginPage() {
      this.$router.push("/login");
    },
  },
};
</script>

